import React from 'react';
import io from './services/socket.io';
import PrivateRoutes from './private.routes'


function App() {
  React.useEffect(()=>{
    io.on('refresh', (e)=>{
      window.location.reload();
    })
  })

  return (
    <div className="App">
      <PrivateRoutes />
    </div>
  );
}

export default App;
